@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
.card {
    background: #fff;
    border-radius: 2px;
    height: 250px;
    margin: 1rem;
    position: relative;
    width: 250px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }

  .card:hover {
    cursor: pointer;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background:#6CADDC;
  }
  .card > .img-container > img {
    width: 100%;
    height: 100%;
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  }
.wrapper {
    padding-top: 50px;
    background: -webkit-linear-gradient(top, #0DCDFF, #0C9BE8, #0C9BE8 );
    background: linear-gradient(to bottom, #0DCDFF, #0C9BE8, #0C9BE8 );
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }
.header {
    width: 100%;
    font-size: 2em;
    color: black;
    margin-bottom: 10px;
    font-family: Georgia, 'Times New Roman', Times, serif
  }

p {
  font-size: 50px;
  font-family: Georgia, 'Times New Roman', Times, serif
}

p1 {
  font-size: 20px;
  margin-top: 0;
}
  
  .title {
    float: left;
  }
  
